@import (reference) 'common/color.less';
.class-box{margin:0 auto;margin-top:50px;}
.serchtext{height:32px;width:0;border:0; }
.on{width:0;border:1px solid #d5d9d0;border-right:0;}
.serch{width:43px;display:inline-block;height:32px;vertical-align:middle;}
.serchimg{width:25px;height:30px;display:inline-block;margin-left:9px;background: url('../../bundle/img/icon.png') no-repeat;background-position:-335px -30px;}
.head{color:#767575;}
.schbox{position:relative}
.listbox{background:white url('../../bundle/img/line.png') no-repeat;position:absolute;top:50px;background-size:100% 100%;width:103px;padding:20px 1px;overflow:hidden  }
.schcontent{overflow:auto;overflow-x:hidden;max-height:240px;width:100%;scrollbar-face-color:red;  }
.schcontent li{height:28px;line-height:28px;cursor: pointer;width:98px;}
.schcontent li:hover{background:#e5e5e5}
.table .scharea{display:inline-block;width:95px;height:23px;border:1px solid #c1c1c1;line-height:23px;text-align:center;position:relative;border-radius:4px;}
.table .scharea:after{content:"";position:absolute;border:5px solid transparent;border-bottom-color:#cb441e;border-right-color:#cb441e;right:0;bottom:0}
.table td{width:20%}
.btn{width:76px;height:23px;background:#cb441e;border-radius:2px;color:white}

